import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return{
            time1: '',
            time2: '',
            number: '',
            loop: false,
            Id: '',
            weekDay: '',
            weekDays: [
                '周一',
                '周二',
                '周三',
                '周四',
                '周五',
                '周六',
                '周日'
            ],
            timing: [
                {
                    val: 'm',
                    name: '上午'
                },
                {
                    val: 'a',
                    name: '下午'
                },
                {
                    val: 'e',
                    name: '晚上'
                }
            ],
            time: '',
            user: this.$store.state.userInfo.Data,
            list:[
                [],
                [
                    '00','30'
                ]
            ],
            arr:[],
            flag: false,
            showConfirm:false,
			FaceId: this.$store.state.FaceId,
        }
    },
    watch: {
        time(v){
			switch(v){
			    case 'm':
			        this.list[0] = ['07','08','09','10','11','12']
			    break;    
			    case 'a':
			        this.list[0] = ['12','13','14','15','16','17','18']
			    break;    
			    case 'e':
			        this.list[0] = ['18','19','20','21','22']
			    break;
			}
        }
    },
    computed:{
        isDisable(){
            return !(this.time1 && this.time2 && this.number && this.weekDay)
        }
    },
    created(){
        if(this.$route.query.id){
            this.Id = this.$route.query.id
            this.pull()
        }
    },
    methods:{
        save(){
            const d = new Date(this.date)
			const TimePeriod = this.time == 'm'?'上午':this.time == 'a'?'下午':this.time == 'e'?'晚上':''
            const IsOn = this.loop ? 1 : 0
            this.$post('AppointmentAPI/FaceConsultation/SaveDoctorCycleDuty',this.$pick({
                CycleDutyId: this.Id,
                DrId: this.user.User.DoctorId,
				DrName:this.user.User.DoctorName,
				FaceConsultationAddressId:this.FaceId,
                WeekDay: this.weekDay,
                StartTime: this.time1,
                EndTime: this.time2,
                Nums: this.number,
                IsOn,
				TimePeriod,
            })).then(o => {
                this.$toast('修改成功')
                this.$router.back()
            })
        },
        deleteSch(){
        	//删除排班
        	this.$post('AppointmentAPI/FaceConsultation/DelDoctorCycleDuty',{
                DrId: this.user.User.DoctorId,
                CycleDutyId: this.Id,
            }).then(o => {
            	this.$toast('已删除该循环排班')
                this.$router.back()
            })
        },
        change(v){
            this.loop = v
        },
        pull(){
            this.$get('AppointmentAPI/FaceConsultation/GetDoctorCycleDuty',{
                CycleDutyId: this.$route.query.id
            }).then(o => {
                if(o.TimePeriod == 1){
                    this.time = 'm'
                }
                else if(o.TimePeriod == 2){
                    this.time = 'a'
                }
                else if(o.TimePeriod == 3){
                    this.time = 'e'
                }
                this.time1 = o.StartTime
                this.time2 = o.EndTime
                this.number = o.Nums
                this.loop = o.IsOn === 0 ? false : true
                this.weekDay = o.WeekDay
            })
        },
        changeTime(){
			if(this.time === 'm'){
				if(this.arr[0] == '12'){
					 this.list[1] = ['00']	
					 this.list = [this.list[0],['00']]
				}else{
					this.list = [this.list[0],['00','30']]
				}
			}else if(this.time === 'a'){
				if(this.arr[0] == '18'){
					 this.list[1] = ['00']	
					 this.list = [this.list[0],['00']]
				}else{
					this.list = [this.list[0],['00','30']]
				}
			}else{
				if(this.arr[0] == '22'){
					 this.list[1] = ['00']	
					 this.list = [this.list[0],['00']]
				}else{
					this.list = [this.list[0],['00','30']]
				}
			}
            if(this.flag === 'start'){
                this.time1 = this.arr.join(':')
            }
            else{
                this.time2 = this.arr.join(':')
            }
        },
        getTime(f){
            this.flag = f
            this.arr = [this.list[0][0],this.list[1][0]]
			this.list = [this.list[0],['00','30']]
            if(this.time === 'm'){
                if(this.flag === 'start'){
                    this.time1 = '07:00'
                }
                else{
                    this.time2 = '07:00'
                }
            }
            else if(this.time === 'a'){
                if(this.flag === 'start'){
                    this.time1 = '12:00'
                }
                else{
                    this.time2 = '12:00'
                }
            }
            else if(this.time === 'e'){
                if(this.flag === 'start'){
                    this.time1 = '18:00'
                }
                else{
                    this.time2 = '18:00'
                }
            }
        },
        reset(){
            this.time1 = null
            this.time2 = null
        }
    }
}



